import { useState, useEffect } from "react";
import Loader from "../../Common/Loader/Loader";
import MovieCard from "../Home/MovieCard/MovieCard";

import classes from "./Filter.module.css";

function Filter() {
    const [searchResults, setSearchResults] = useState([]);
    const [loading, setLoading] = useState(true);
    const [searchInput, setSearchInput] = useState('');

    useEffect(() => {
        fetch('https://cars-react-app-server.herokuapp.com/data/movies')
            .then(response => response.json())
            .then(result => {
                const results = result.reduce((accumulator, object) => {
                    let str = '';
                    for (let key in object) {
                        if (key === 'title' || key === 'description') {
                            str += object[key];
                        }
                    }

                    if (str.toLowerCase().includes(searchInput.toLowerCase())) {
                        accumulator.push(object);
                    }

                    return accumulator;
                }, []);

                // console.log(results);
                setSearchResults(results);
            })
            .catch(err => console.error(err))
            .finally(setLoading(false));
    }, [searchInput]);

    return (
        <div className={classes.main}>
            <form className={`form-group ${classes.hasSearch}`}>
                <h3 className={classes.field}>Search Your Favourite Movie</h3>
                <span className={classes.formControlFeedback}></span>
                <input
                    type="text"
                    className={classes.formControl}
                    placeholder="Search"
                    value={searchInput}
                    onChange={e => setSearchInput(e.target.value)}
                />
            </form>
            <div className={classes.flex}>
                {
                    loading
                        ? <Loader />
                        : !searchResults.length
                            ? <p className={classes.notFound}>No currently found results from your search :(</p>
                            : searchResults.map((result, index) => (
                                <MovieCard key={index} {...result} />
                            ))
                }
            </div>
        </div>
    )
}

export default Filter;