async function request(URL, configOptions) {
    try {
        const response = await fetch(URL, configOptions);

        if (!response.ok) {
            throw new Error((await response.json()).message || 'Something went wrong!');
        } else {
            const data = await response.json();

            return data;
        }
    } catch (error) {
        alert(error.message);
    }
}

export default request;