import style from './Modal.module.css';

function Modal({ message, closeModalClickHandler, cancelActionHandler }) {
    return (
        <div className={style.overlay}>
            <div className={style.modal}>
                <p className={style.message}>{message}</p>
                <button onClick={closeModalClickHandler} className={style.action}>Okay</button>
                { cancelActionHandler ? <button onClick={cancelActionHandler} className={style.action}>Cancel</button> : null }
            </div>
        </div>
    );
}

export default Modal;