import MoviesForum from './MoviesForum/MoviesForum';
import MovieCard from './MovieCard/MovieCard';


import Loader from '../../Common/Loader/Loader';
import { useEffect, useState } from 'react';

function Home() {
    const [movieArticles, setMoviesArticles] = useState([]);
    const [isLoading, setIsLoading] = useState(true);

    useEffect(() => {
        setIsLoading(true)
        fetch('https://cars-react-app-server.herokuapp.com/data/movies')
            .then(res => res.json())
            .then(movies => {
                setMoviesArticles(movies);
            })
            .catch(err => alert(err.message))
            .finally(setIsLoading(false));
    }, []);

    if (isLoading) {
        return <Loader />
    }

    return (
        <>
            <MoviesForum />
            <section>
                <div>
                    <h1 style={{
                        fontSize: 40,
                        fontWeight: 550,
                        textAlign: 'center',
                        marginTop: 40
                    }}>Movie Articles</h1>
                    <div className="card-wrapper">
                        {
                            movieArticles.map((movieProps, index) => <MovieCard key={index} {...movieProps} />)
                        }
                    </div>
                </div>
            </section>
        </>
    );
}

export default Home;