export const REDUCER_ACTIONS = {
    GET_MOVIE_ARTICLE: 'get-movie-article',
    LOAD_MOVIE_ARTICLE_COMMENTS: 'load-movie-article-comments',
    GET_MOVIE_ARTICLE_LIKES_FROM_USER: 'get-movie-article-likes-from-user',
    MODIFY_MOVIE_ARTICLE_COMMENT: 'modify-movie-article-comment',
    LIKE_MOVIE_ARTICLE: 'like-movie-article',
    GET_MOVIE_ARTICLE_LIKES: 'get-movie-article-likes',
}

export const initialMovieDetailsState = {
    movieArticle: {},
    movieArticleComments: [],
    movieArticleLikesFromUser: [],
    movieArticleLikes: 0,
    areCommentsLoading: true,
    isFormSubmited: false,
    isLiked: false,
    isMovieLoading: true,

}

export default function reducer(state, { type, payload }) {
    switch (type) {
        case REDUCER_ACTIONS.GET_MOVIE_ARTICLE:
            return {
                ...state,
                movieArticle: {
                    _onwerId: payload._ownerId,
                    title: payload.title,
                    description: payload.description,
                    img: payload.img,
                    _id: payload._id
                },
                isMovieLoading: false,
            }
        case REDUCER_ACTIONS.LOAD_MOVIE_ARTICLE_COMMENTS:
            return {
                ...state,
                areCommentsLoading: false,
                isFormSubmited: false,
                movieArticleComments: payload.comments
            }
        case REDUCER_ACTIONS.GET_MOVIE_ARTICLE_LIKES_FROM_USER:
            return {
                ...state,
                movieArticleLikesFromUser: payload.userLikes,
                isLiked: false
            }
        case REDUCER_ACTIONS.MODIFY_MOVIE_ARTICLE_COMMENT:
            return {
                ...state,
                isFormSubmited: true
            }
        case REDUCER_ACTIONS.LIKE_MOVIE_ARTICLE:
            return {
                ...state,
                isLiked: true
            }
        default:
            return state;
    }
}