import { createSlice } from '@reduxjs/toolkit';

const initialAuthState = {
    isAuthenticated: false,
    userAuthState: {},
    error: null
}

export const authenticate = (userDataObj, authType) => {
    return async function(dispatch) {
        try {
            const response = await fetch(`https://cars-react-app-server.herokuapp.com/users/${authType}`, {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify(userDataObj)
            });
            const userData = await response.json();

            if (response.ok === false) {
                throw new Error('Failed while authenticating!');
            } else {
                dispatch(authSlice.actions.handleAuthenticate({
                    authToken: userData.accessToken,
                    userId: userData._id,
                    username: userData.username
                }));
            }
        } catch (error) {
            // console.error(error);
            dispatch(authSlice.actions.handleErrors({
                error: {
                    message: error.message,
                }
            }));
        }
    }
}

const authSlice = createSlice({
    name: 'auth-slice',
    initialState: initialAuthState,
    reducers: {
        handleAuthenticate(state, { payload }) {
            state.userAuthState = {
                authToken: payload.authToken,
                userId: payload.userId,
                username: payload.username
            };
            state.isAuthenticated = true;
        },
        handleLogout(state) {
            state = initialAuthState;
        },
        handleErrors(state, { payload }) {
            state.error = payload.error;
        }
    },
});

export const authSliceActions = authSlice.actions;

export default authSlice;