import { useEffect, useReducer, Fragment, createContext } from 'react';

import { useParams } from 'react-router-dom';
import style from './Details.module.css';

import CommentSection from './CommentSection/CommentSection';
import DetailsButtons from './DetailsButtons/DetailsButtons';

import reducer, { initialMovieDetailsState, REDUCER_ACTIONS } from "./reducer";

import Loader from '../../Common/Loader/Loader';
import request from '../../../utils/api';

export const DetailsContextProvider = createContext('NO CURRENT CONTEXT PROVIDER');

function Details() {
    const { movieId } = useParams();
    const [details, dispatch] = useReducer(reducer, initialMovieDetailsState);


    useEffect(() => {
        const fetchMovieArticleDetails = async () => {
            const movieDetails = await request(`https://cars-react-app-server.herokuapp.com/data/movies/${movieId}`);
            
            return dispatch({
                type: REDUCER_ACTIONS.GET_MOVIE_ARTICLE,
                payload: {
                    _ownerId: movieDetails._ownerId,
                    title: movieDetails.title,
                    description: movieDetails.description,
                    img: movieDetails.img,
                    _id: movieDetails._id
                }
            });

        };

        fetchMovieArticleDetails();
    }, [movieId]);

    return (
        <Fragment>
            {details.isMovieLoading
                ? <Loader />
                : <section>
                    <div className={style.movieContainer}>
                        <h1 className={style.movieDetailsTitle}>Movie title: {details.movieArticle.title}</h1>
                        <img className={style.movieDetailsImg} src={details.movieArticle.img} alt="movie" />
                        <div className={style.movieDetailsInfo}>
                            <h3 className={style.movieDetailsInfoImg}>Movie Description</h3>
                            <p className={style.movieDetailsInfoParagraph}>
                                {details.movieArticle.description}
                            </p>
                            <DetailsContextProvider.Provider value={{
                                ...details,
                                dispatch
                            }}>
                                <DetailsButtons />
                                <CommentSection />
                            </DetailsContextProvider.Provider>
                        </div>
                    </div>
                </section>
            }
        </Fragment>
    );
}


export default Details;
