import style from '../Decorate.module.css';
import { useSelector } from 'react-redux';

import useForm from '../../../CustomHooks/useForm';


function Create({
    history
}) {
    const userAuthToken = useSelector(state => state.auth.userAuthState.authToken);

    const [ values, handleChange, handleSubmit ] = useForm({
        title: '',
        description: '',
        image: '',
    }, 'https://cars-react-app-server.herokuapp.com/data/movies', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json', 'X-Authorization': userAuthToken }
    });

    return (
        <section className={style.createPage}>
            <div className={style.createFormWrapper}>
                <h2 className={style.createFormTitle}>CREATE MOVIE ARTICLE</h2>
                <form onSubmit={e => handleSubmit(e).then(history.push('/'))}>
                    <input className={style.movieTitle}
                        type="text"
                        name="title"
                        placeholder="ENTER MOVIE TITLE"
                        onChange={handleChange}
                        value={values.movieTitle}
                    />
                    <textarea className={style.movieDescription}
                        type="text"
                        name="description"
                        placeholder="ENTER MOVIE DESCRIPTION"
                        rows="4"
                        columns="50"
                        onChange={handleChange}
                        value={values.movieDescription}
                    >
                    </textarea>
                    <input className={style.movieImageURL}
                        type="text"
                        name="image"
                        placeholder="ENTER MOVIE IMAGE URL"
                        onChange={handleChange}
                        value={values.movieImage}
                    />
                    <button className={style.createMovieBtn} type="submit">CREATE</button>
                </form>
            </div>
        </section>
    );
}

export default Create;